* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  list-style: none;
  padding: 0px;
}

.country-list li{
  display: flex;
}

.country-list img{
  width: 100px;
  margin-right: 10px;
}

.country-info div{
  margin-bottom: 10px;
}

.country-info .country{
  display: flex;
  gap: 10px;
}
